/*Body and Grid Items*/
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}
.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto auto;
  grid-gap: 5px;
  width: 80%;
  padding: 0px 10%
}
.grid-container > div {
  background-color: rgba(255, 255, 255, 0.5);
  text-align: center;
  font-size: 30px;
}
.grid-container span::after {
  content: "\00b0";
}
.grid-container:nth-child(1), .App-degrees {
   border-top: 1px solid lightgray;
}



