.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  height: 30%;
  padding: 20px;
  color: white;
  font-family: 'Open Sans', sans-serif;
}
.App-header {
  color: #7ED2E9;
}
.App-header, .App-sms {
  background-color: #222;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.App-weather {
 height: 35%;
}
.App-temp {
  font-size: 4em;
  color: #7ED2E9;
  padding: 15px;
  margin: 0;
}
.App-time, .App-degrees {
   margin:-5px;
   padding:10px 0px
}
.App-time {
  font-size: 0.7em;
}
.App-icons {
  width: 27px;
  mix-blend-mode: color-dodge;
}
.App-degrees {
  color: lightgray;
  font-size: 0.7em;
}
.App-btn {
  padding: 6px 50px;
  border: transparent;
  border-radius: 2px;
 }
.App-sms {
 height: 200px;
}
.App-sms a {
  color: #fff;
  background: #7ED2E9;
  padding: 6px 68px;
  border: transparent;
  border-radius: 2px;
  margin: -7px;
  display: inline-block;
  text-decoration: none;
}
.App-sms input {
 min-width: 150px;
 padding: 7px 27px;
 margin-bottom: 14px;
}
 
.App-sms h2 {
  color: #fff;
  padding-top: 1.5em
}
 
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
 
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



